import React, { useState } from "react";

const FileUpload = () => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files[0]) {
      setSelectedFile(event.target.files[0]);
    }
  };

  const handleDeleteFile = () => {
    setSelectedFile(null); // Resetujemy stan, co usuwa plik
  };

  return (
    <div className="flex flex-col items-center justify-center h-[40vh] pt-20">
      {/* Upload Container */}
      <div className="bg-gray-600 dark:bg-gray-900 p-6 rounded-lg shadow-lg w-1/2 text-center">
        <h2 className="text-xl font-semibold text-white">Upload your file</h2>

        {/* File Input */}
        <label
          htmlFor="file-upload"
          className="mt-4 flex flex-col items-center justify-center p-6 border border-dashed border-gray-300 dark:border-gray-700 rounded-lg cursor-pointer hover:bg-gray-700 transition-all"
        >
          <span className="mt-2 text-gray-300">Click or drag a file to upload</span>
          <input
            id="file-upload"
            type="file"
            className="hidden"
            onChange={handleFileChange}
          />
        </label>

        {/* Wy≈õwietlanie informacji o pliku */}
        {selectedFile && <Data file={selectedFile} onDelete={handleDeleteFile} />}
      </div>
    </div>
  );
};

export default FileUpload;
