import React from 'react'
import { Button } from './ui/button';

interface DataVisualizeProps {
  file: File;
}

const DataVisualize: React.FC<DataVisualizeProps> = ({ file }) => {
  return (
    <div className="flex items-center justify-center gap-4 bg-gray-800 p-4 rounded-lg">
  {/* Informacje o pliku */}
  <div className="text-white">
    <h2 className="text-lg font-semibold">File Name: {file.name}</h2>
    <p className="text-sm text-gray-400">Size: {(file.size / 1000).toFixed(2)} kB</p>
  </div>

  {/* Przycisk usuwania */}
  <Button 
    className="bg-red-600 text-white hover:bg-red-700 transition-all"
    onClick={() => console.log("Delete button clicked")}
  >
    Delete
  </Button>
</div>

  );
};

export default DataVisualize